<template>
  <div class="login-page">
    <div class="layer bg" id="login"></div>
    <div class="layer flex-center">
      <!-- logo -->
      <div class="logo-group">
        <img src="./image/logo.png" alt="logo">
      </div>
      <!--  -->
      <div class="form-group">
        <el-card>
          <el-form ref="loginForm" label-position="top" :rules="rules" :model="formLogin">
            <el-form-item  prop="username">
              <el-input type="text" v-model="formLogin.username" placeholder="">
                <i slot="prepend" class="fa fa-user-circle-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="formLogin.password" placeholder="">
                <i slot="prepend" class="fa fa-keyboard-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input type="text" v-model="formLogin.code" placeholder="- - - -">
                <template slot="prepend"></template>
                <template slot="append">
                  <img class="login-code" src="./image/login-code.png">
                </template>
              </el-input>
            </el-form-item>
            <el-button @click="submit" type="primary" class="button-login"></el-button>
          </el-form>
        </el-card>
      </div>
      <!--  -->
      <el-button type="info" class="button-help" @click="dialogVisible = true">
        
      </el-button>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="400px">
      <el-row :gutter="10" style="margin: -20px 0px -10px 0px;">
        <el-col v-for="(user, index) in users" :key="index" :span="8">
          <div class="user-btn" @click="handleUserBtnClick(user)">
            <d2-icon name="user-circle-o"/>
            <span>{{user.name}}</span>
          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
require('particles.js')
import config from './config/default'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      // 
      dialogVisible: false,
      users: [
        {
          name: '',
          username: 'admin',
          password: 'admin'
        },
        {
          name: '',
          username: 'editor',
          password: 'editor'
        },
        {
          name: '1',
          username: 'user1',
          password: 'user1'
        }
      ],
      // 
      formLogin: {
        username: 'admin',
        password: 'admin',
        code: 'v9am'
      },
      // 
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    // 
    particlesJS('login', config)
  },
  methods: {
    ...mapActions([
      'd2adminLogin'
    ]),
    /**
     * @description 
     * @param {Object} user 
     */
    handleUserBtnClick (user) {
      this.formLogin.username = user.username
      this.formLogin.password = user.password
      this.submit()
    },
    /**
     * @description 
     */
    // 
    submit () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 
          //  
          // 
          this.d2adminLogin({
            vm: this,
            username: this.formLogin.username,
            password: this.formLogin.password
          })
        } else {
          // 
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import './style.scss';
</style>
